@font-face {
  font-family: "DTMFont";
  src: url("./fonts/DTM-Mono.otf") format("truetype");
}

.button-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  border: none;
  border-image: url("./../../public/images/btn.png") round;
  border-image-slice: calc(80 / 184 * 100%) calc(130 / 284 * 100%) fill;
  border-image-width: 30px 48px;
  width: 250px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 25%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  background-color: transparent;
  opacity: 1;
  transition: opacity 0.2s ease-in-out;
  font-family: "DTMFont", sans-serif;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
}

.button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 1;
  pointer-events: none;
}

.button:hover::before {
  background-color: rgba(0, 0, 0, 0.1);
}

.button:hover {
  opacity: 0.7;
  cursor: pointer;
  background-color: transparent;
  color: rgb(220, 220, 220);
}

.button img {
  pointer-events: none;
}

.button:hover img {
  opacity: 1;
}

.button:active img {
  opacity: 0.4;
  filter: alpha(opacity=40);
}

.text {
  font-family: "Open Sans", sans-serif;
  text-shadow: 1px 1px 1px black;
}
