@import url('http://fonts.googleapis.com/css?family=Press+Start+2P');
.container {
}

.container .unityWrapper {
  background: #131314;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}


.container .unityWrapper .loaderContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background: radial-gradient(50% 50% at 50% 50%, #2C4975 0%, rgba(29, 49, 79, 0) 100%),
    url("./../public/images/Frame-367.png") no-repeat bottom center;
}

.container .unityWrapper .loaderContainer .loaderWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 0 90px;
}

.text {
  color: #FFFFFF;
/*   шрифты тоже прикрутите те, которые нужны */
  font-family: 'Press Start 2P';
  font-size: 20px;
  clip-path: inset(0 0.86ch 0 0);
  animation: textLoading 1s steps(6) infinite;
  margin-bottom: 16px;
}

@keyframes textLoading {
  to {
    clip-path: inset(0 -1ch 0 0)
  }
}

.container .unityWrapper .loaderContainer .loadingBar {
  position: relative;
  width: 300px;
  border: 1px solid rgba(72, 124, 171, 0.58);
  border-radius: 9px;
  padding: 2px;
}

.container .unityWrapper .loaderContainer .loadingBar .loadingbarfillBg {
  background: linear-gradient(0deg, #858dd615, #858dd615);
  /* background: linear-gradient(0deg, #858DD6, #858DD6); */
  width: 100%;
  height: 15px;
  opacity: 1;
  border-radius: 20px;
}

.loadingbarimg {
  height: 15px;
  display: block;
  position: absolute;
  top: 2px;
  object-fit: cover;
  left: 2px;
  border-radius: 25px;
  opacity: 0.9;
}

.container .unityWrapper canvas {
  width: 100%;
  height: 100%;
}
